            "
          >
            Cost Range in $AUD (0-150):
          </div>
          <!-- <div class="category">
            <el-select v-model="select" slot="prepend" placeholder="Game Type">
              <el-option
                v-for="(item, index) in category"
                :key="index"
                :label="item.category"
                :value="index"
              ></el-option>
            </el-select>
          </div> -->
        </div>
      </div>
      <div class="gamelist" ref="list">
        <div class="slide" v-for="item in show" :key="item.game_id">
          <el-popover
            placement="top-start"
            width="300"
            trigger="hover"
            :content="item.Description"
          >
            <div class="image" slot="reference">
              <el-image
                :src="item['Image URL'].trim()"
                style="width: 100%; height: 100%"
              >
                <div slot="placeholder" class="image-slot">
                  loading<span class="dot">...</span>
                </div>
              </el-image>
            </div>
          </el-popover>
          <h2>{{ item.Name }}</h2>
          <p style="color: gray">{{ item.Category }}</p>
          <p style="color: white">A${{ item["Cost in AUD"] }}</p>
          <div class="tags">
            <a :href="item['Shopping URL']" target="__blank">Get Game</a>
          </div>
          <div class="btn" @click="addtocart(item)">
            <div v-if="!$store.state.games[item.Name]">
              <el-tooltip
                class="item"
                effect="dark"
                content="Add to Plan"
                placement="top"
              >
                <el-button
                  type="warning"
                  icon="el-icon-star-off"
                  circle
                ></el-button>
              </el-tooltip>
            </div>
            <div v-else>
              <el-tooltip
                class="item"
                effect="light"
                content="Delete from Plan"
                placement="bottom"
              >
                <el-button
                  type="success"
                  icon="el-icon-check"
                  circle
                ></el-button>
              </el-tooltip>
            </div>
          </div>
        </div>
