      <!-- <p style="font-family: Arial; text-align: center">
        *All costs displayed for games are listed in AUD ($)
      </p> -->
    </div>
  </div>
</template>

<script>
import { allgame, gamecategory } from "@/network/api.js";
import dv from "../../../components/exc/dv.vue";

let r;
export default {
  components: { dv },
  async created() {
    r = await allgame();
    this.show = r;
    // console.log(r);
    this.category = await gamecategory();
    this.category.forEach((item, index) => {
      this.list.push(item.category);
      this.cate.push({
        value: item.category,
        id: index,
      });
    });
    // console.log(this.category)
  },
  methods: {
    searchbtn() {
      // console.log(this.gameterm)
      if (this.list.includes(this.gameterm)) {
        this.title = this.gameterm;
        this.show = r.filter(
          (item) =>
            item.Category == this.gameterm &&
            item["Cost in AUD"] <= this.value[1] &&
            item["Cost in AUD"] >= this.value[0]
        );
        return;
      }
    },
    querySearch(queryString, cb) {
      let genre = this.cate;
      let results = queryString
        ? genre.filter(this.createFilter(queryString))
        : genre;
      cb(results);
    },
    // filter the genre when user typing
    createFilter(queryString) {
      return (cate) => {
        return (
          cate.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    getall() {
      if (this.get == "More Games") {
        this.$refs.list.style.height = "100%";
        this.get = "Less Games";
        $('html,body').animate({scrollTop: 1000}, "slow");
      } else {
        this.$refs.list.style.height = "800px";
        this.get = "More Games";
      }
    },
    addtocart(item) {
      if (!this.$store.state.games[item.Name]) {
        this.open3();
        this.$store.commit("modgame", item);
      } else {
        this.$confirm('Are you sure to delete from plan?', {
          confirmButtonText: 'Yes',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(() => {
          this.$store.commit("modgame", item);
          this.open5()
        }).catch(() => {         
        });
      }
      
    },
    costchange() {
      if (this.select !== "") {
        this.show = r.filter(
          (item) =>
            item.Category == this.category[Number(this.select)].category &&
            item["Cost in AUD"] <= this.value[1] &&
            item["Cost in AUD"] >= this.value[0]
        );
      } else {
        this.show = r.filter(
          (item) =>
            item["Cost in AUD"] <= this.value[1] &&
            item["Cost in AUD"] >= this.value[0]
        );
      }
    },
    open3() {
      this.$notify({
        title: "Success!",
        message: "Game successfully added to your wishlist",
        type: "success",
        offset: 200,
      });
    },
    open5() {
      this.$notify.error({
        title: "Gone!",
        message: "Game successfully deleted from your wishlist",
        offset: 200,
      });
    },
  },
  data() {
    return {
      show: [],
      value: [0, 150],
      select: "",
      category: [],
      title: "Games You Might Like",
      get: "More Games",
      gameterm: "",
      cate: [],
      list: [],
    };
  },
  watch: {
    // select(newVal, oldVal) {
    //   // console.log(this.category[newVal].category)
    //   this.title = this.category[newVal].category;
    //   this.show = r.filter(
    //     (item) =>
    //       item.Category == this.category[newVal].category &&
    //       item["Cost in AUD"] <= this.value[1] &&
    //       item["Cost in AUD"] >= this.value[0]
    //   );
    // },
  },
};
</script>
<style scoped>
